body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Lato',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  border: none;
  background-color: transparent; 
}


:root {
  /* COLORS */
  --text-color: #000000;
  --body-color: #ffffff;

  --teal-color: #3B8A7F;
  --red-color: #CA5B33;
  --warm-color: #B04025;
  --grey-color: #ECF0F2;
  --black-color: #0D0D0D;

  /* FONTS */
  --body-font: "Lato", sans-serif;
  --h1-font: "Lato", sans-serif;
  --h1-font-size: 32px;
  --h2-font-size: 24px;
  --normal-font-size: 15px;
  --small-font-size: 14px;

  /* FONT WEIGHTS */
  --font-light: 300;
  --font-regular: 400;
  --font-medium: 500;
  --font-semibold: 600;

}

* {
  padding: 0;
  box-sizing: border-box;
  line-height: 1.5;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 8%; 
}

body {
  margin: 0 500px;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  background-color: var(--body-color);
  color: var(--text-color);
  font-weight: var(--font-regular);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: var(--text-color);
  text-decoration: none;
}


h1, h2 {
  font-family: var(--h1-font);
  font-weight: var(--font-semibold);
}

h3 {
  font-weight: var(--font-medium);
  font-size: var(--normal-font-size);
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

button, input {
  padding: 10px 50px;
  border: none;
  border-radius: 7px;
  justify-content: center;
  font-size: 20px;
  margin-bottom: 20px;
}

input:focus {
  outline: 0;
}

.btn {
  cursor: pointer;
  background: var(--teal-color);
  color: white;
  width: 200px;
  text-align: center;

}

@media screen and (max-width: 1280px) {
  body {
    margin: 0;
    font-family: var(--body-font);
    font-size: var(--normal-font-size);
    background-color: var(--body-color);
    color: var(--text-color);
    font-weight: var(--font-regular);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /* margin: 0 50px; */
    /* width: 100%; */
  }

  
  button, input {
    padding: 10px 20px;
    border: none;
    border-radius: 7px;
    justify-content: center;
    font-size: 20px;
    margin-bottom: 20px;
  }
  
  input:focus {
    outline: 0;
  }
  
  .btn {
    cursor: pointer;
    background: var(--teal-color);
    color: white;
    width: 120px;
    text-align: center;
    font-size: 14px;
  }
}  